"use client";

import React, { useState, useRef, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Expand, Shrink, PictureInPicture, VideoOff } from 'lucide-react';
import { useToast } from "@/hooks/use-toast";

interface CameraFeedProps {
  id: string;
  name: string;
  posterUrl: string;
  dataAiHint?: string;
}

export function CameraFeed({ id, name, posterUrl, dataAiHint }: CameraFeedProps) {
  const { toast } = useToast();
  const videoRef = useRef<HTMLVideoElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const [isPipActive, setIsPipActive] = useState(false);
  const [isFullscreenActive, setIsFullscreenActive] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [pipAvailable, setPipAvailable] = useState(false);
  const [fullscreenAvailable, setFullscreenAvailable] = useState(false);

  useEffect(() => {
    setPipAvailable(typeof document !== 'undefined' && !!document.pictureInPictureEnabled);
    setFullscreenAvailable(typeof document !== 'undefined' && !!document.fullscreenEnabled);

    const videoElement = videoRef.current;
    if (!videoElement) return;

    const handleLoadedData = () => setIsLoading(false);
    const handleEnterPip = () => setIsPipActive(true);
    const handleLeavePip = () => setIsPipActive(false);
    
    videoElement.addEventListener('loadeddata', handleLoadedData);
    videoElement.addEventListener('enterpictureinpicture', handleEnterPip);
    videoElement.addEventListener('leavepictureinpicture', handleLeavePip);

    return () => {
      videoElement.removeEventListener('loadeddata', handleLoadedData);
      videoElement.removeEventListener('enterpictureinpicture', handleEnterPip);
      videoElement.removeEventListener('leavepictureinpicture', handleLeavePip);
    };
  }, []);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.load(); // Force the video element to reload when posterUrl changes
      setIsLoading(true); // Reset loading state
    }
  }, [posterUrl]);

  useEffect(() => {
    const handleFullscreenChange = () => {
      if (typeof document !== 'undefined') {
        setIsFullscreenActive(!!document.fullscreenElement);
      }
    };
    if (typeof document !== 'undefined') {
      document.addEventListener('fullscreenchange', handleFullscreenChange);
    }
    return () => {
      if (typeof document !== 'undefined') {
        document.removeEventListener('fullscreenchange', handleFullscreenChange);
      }
    };
  }, []);

  const togglePip = async () => {
    if (!pipAvailable) {
      toast({ title: "PiP Not Supported", description: "Your browser does not support Picture-in-Picture.", variant: "destructive" });
      return;
    }
    try {
      if (videoRef.current && typeof document !== 'undefined') {
        if (document.pictureInPictureElement === videoRef.current) {
          await document.exitPictureInPicture();
        } else {
          if (videoRef.current.readyState >= 1) {
            await videoRef.current.requestPictureInPicture();
          } else {
            toast({ title: "PiP Not Ready", description: "Video is still loading. Please try again in a moment.", variant: "default" });
          }
        }
      }
    } catch (error) {
      console.error("Error toggling PiP:", error);
      if (error instanceof DOMException && error.message.includes("Metadata for the video element are not loaded yet")) {
        toast({ title: "PiP Not Ready", description: "Video metadata is still loading. Please try again shortly.", variant: "default" });
      } else {
        toast({ title: "PiP Error", description: "Could not enter Picture-in-Picture mode.", variant: "destructive" });
      }
    }
  };

  const toggleFullscreen = async () => {
    if (!fullscreenAvailable) {
      toast({ title: "Fullscreen Not Supported", description: "Your browser does not support Fullscreen mode.", variant: "destructive" });
      return;
    }
    try {
      if (containerRef.current && typeof document !== 'undefined') {
        if (!document.fullscreenElement) {
          await containerRef.current.requestFullscreen();
        } else {
          if (document.exitFullscreen) {
            await document.exitFullscreen();
          }
        }
      }
    } catch (error) {
      console.error("Error toggling Fullscreen:", error);
      toast({ title: "Fullscreen Error", description: "Could not enter Fullscreen mode.", variant: "destructive" });
    }
  };

  return (
    <Card ref={containerRef} className="shadow-md w-full h-full flex flex-col bg-card">
      <CardHeader className="py-3 px-4">
        <CardTitle className="text-base font-medium truncate" title={name}>{name}</CardTitle>
      </CardHeader>
      <CardContent className="p-0 relative flex-grow">
        <div className="aspect-video bg-muted rounded-b-lg overflow-hidden relative">
          {isLoading && (
            <div className="absolute inset-0 flex items-center justify-center bg-black/50 z-10">
              <VideoOff className="w-12 h-12 text-muted-foreground animate-pulse" />
            </div>
          )}
          <video
            ref={videoRef}
            poster={posterUrl}
            className="w-full h-full object-cover"
            playsInline
            muted
            loop 
            autoPlay 
            data-ai-hint={dataAiHint || 'camera feed'}
            controls={false} 
            onCanPlay={() => setIsLoading(false)} 
            onLoadedData={() => setIsLoading(false)} 
            onError={() => {
              setIsLoading(false); 
              toast({ title: "Video Error", description: `Could not load video for ${name}.`, variant: "destructive" });
            }}
            src={posterUrl}
            onLoadStart={() => setIsLoading(true)} // Set loading state when video starts loading
          >
          </video>
          <div className="absolute bottom-2 right-2 flex space-x-2">
            {pipAvailable && (
              <Button variant="outline" size="icon" onClick={togglePip} aria-label={isPipActive ? "Exit Picture-in-Picture" : "Enter Picture-in-Picture"} className="bg-black/50 hover:bg-black/70 text-white" >
                <PictureInPicture className="h-4 w-4" />
              </Button>
            )}
            {fullscreenAvailable && (
              <Button variant="outline" size="icon" onClick={toggleFullscreen} aria-label={isFullscreenActive ? "Exit Fullscreen" : "Enter Fullscreen"} className="bg-black/50 hover:bg-black/70 text-white">
                {isFullscreenActive ? <Shrink className="h-4 w-4" /> : <Expand className="h-4 w-4" />}
              </Button>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
